<template>
    <div v-if="Product.name">
        <h2></h2><br><br>
        <div class="row">
            <div class="col-md-3"></div>
            <div class="col-md-6">
                <h5 class="float-left">1.  </h5>
                <table class="table">
                    <thead>
                    <tr class="table-active">
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr>
                        <td>{{Product.name}}</td>
                        <td>{{(Product.price*quantity).toLocaleString()}}</td>
                        <td>{{quantity}}</td>
                        <td>{{Product.seller}}</td>
                    </tr>
                    </tbody>
                </table>
                <br><br>
                <h5 class="float-left">2.   </h5>
                <table class="table">
                    <thead>
                        <!--
                        <th scope="col">
                            <form name="form" id="form" method="post">
                                <input type="text" name="currentPage" value="1"/> <--    ( . currentPage : n > 0)
                                <input type="text" name="countPerPage" value="10"/>    (  . countPerPage  : 0 < n <= 100)
                                <input type="text" name="resultType" value="json"/> <--    ( , json)
                                <input type="text" name="confmKey" value="U01TX0FVVEgyMDE5MDcxMDE2MzIwMTEwODg3MTk="/>    ()
                                <input type="text" name="keyword" value="" onkeydown="enterSearch();"/>    ()
                                <input type="button" onClick="getAddr();" value=""/>
                                <div id="list" ></div>    
                            </form>
                        </th>
                        -->
                    <tr>
                        <th class="table-active" scope="row"><h6> </h6></th>
                        <th scope="col">
                            <input type="text" v-model="addrDetail" id="addrDetail" class="form-control"  name="addrDetail" placeholder=" ">
                        </th>
                    </tr>
                    <tr>
                        <th class="table-active" scope="row"><h6></h6></th>
                        <th scope="col">
                            <input type="text" class="form-control"  v-model="zipNo" id="zipNo" name="zipNo" placeholder="">
                        </th>
                    </tr>
                    <tr>
                        <th class="table-active" scope="row"><h6></h6></th>
                        <th scope="col">
                            <input type="text" class="form-control" v-model="orderTel" placeholder="01012341234">
                        </th>
                    </tr>
                    </thead>
                </table>
                <br><br>
                <button @click="newOrderSubmit" type="button" class="btn btn-primary"></button>
            </div>
            <div class="col-md-3"></div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "CreateOrder",
        data () {
            return {
                Product : {},
                quantity : this.$route.params.quantity,
                roadFullAddr : '',
                roadAddrPart1 : '',
                roadAddrPart2 : '',
                addrDetail : '',
                zipNo : '',
                unum : Number,
                orderTel : ''
            }
        },
        created() {
            this.$store.dispatch('GetProfile')
                .then( response => {
                    console.log(' '+JSON.stringify(response.data.user));
                    this.unum = response.data.user.number;
                    let product = {
                        productcode : this.$route.params.product,
                        quantity : this.$route.params.quantity
                    };
                    return this.$store.dispatch('GetProductDetail', product);
                }, err => {
                    console.log(' ' + err);
                    this.$store.dispatch('LOGOUT');
                    this.$router.replace({path : '/Login'});
                })
                .then( res => {
                    console.log('');
                    console.log('response : '+JSON.stringify(res));
                    this.Product = res.data.result;
                })
                .catch( err => {
                    console.log(' ' + err);
                    this.$store.dispatch('LOGOUT');
                    this.$router.replace({path : '/Login'});
                });
        },
        methods : {
            goPopup : function () {
                var pop = window.open("jusoPopup.jsp","pop","width=570,height=420, scrollbars=yes, resizable=yes");
            },
            newOrderSubmit : function () {
                if ( this.addrDetail == '' || this.orderTel == '' ) {
                    alert('  ');
                    console.log('validate fail');
                } else {
                    let newOrder = {
                        product: this.Product.productcode + '/' + this.quantity,
                        price: this.Product.price * this.quantity,
                        orderer: this.unum,
                        delivery_address: this.addrDetail,
                        delivery_tel: this.orderTel
                    };
                    console.log(JSON.stringify(newOrder));

                    this.$store.dispatch('newOrder', newOrder)
                        .then( response => {
                            if(response.data.success == true) {
                                alert('Order Success');
                                console.log('Order Success');
                            } else {
                                alert('Order Failure');
                                console.log('Order Failure : '+ JSON.stringify(response))
                            }
                        }).catch( err => {
                        console.log('Order Err : '+ err);
                    });
                }
            }
        }
    }
</script>

